body {
  background: yellowgreen;
}
footer {
  position: fixed;
  bottom: 0;
  color: white;
  background: lime;
  text-shadow: 2px 2px 2px red;
  width: 100%;
  left: 0;
}
.center {
  text-align: center;
}
.img {
  width: 40%;
  height: 40%;
}
.header {
  position: sticky;
  top: 0;
  background: lime;
  color: white;
  text-shadow: 2px 2px 2px red;
  margin: 0 0 40px 0;
}
.btn {
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 5px;
  margin: 10px;
  background: rgb(47, 255, 92);
  color: white;
  font-weight: 900;
  text-shadow: 2px 2px 2px black;
}
.blue {
  color: blue;
  font-weight: 900;
}
.datepicker {
  width: 132px;
  font-weight: 900;
}
.fade {
  animation: fadeIn 3s 1;
}
@keyframes fadeIn {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}
@media screen and (max-width: 344px) {
  .birthString {
    font-size: 28px;
  }
}
